import React, { useEffect, useMemo, useRef, useState } from 'react'
import groupBy from 'lodash/groupBy'
import last from 'lodash/last'
import styled from 'styled-components'

import Box from 'components/Box'
import Flex from 'components/Flex'
import sortSize from 'utils/sortSize'
import Text from 'components/Text'

import PageSimulator from './PageSimulator'
import NoteBox from './NoteBox'

const getGroupItems = (data, groupMap, groupKey) => Object.keys(groupBy(data, groupKey)).reduce((all, key) => {
  const index = groupMap?.[key] || 1
  all[index - 1] = all[index - 1] || []
  all[index - 1].push(key)
  return all
}, [])

const joinName = (names, glue) => {
  if (names.length === 1) return names[0]
  const units = names.map(n => n.replace(/[0-9.]/g, ''))
  const commonUnit = units.reduce((all, u) => {
    if (!all) return u
    let s = 0
    while (!u.substring(0, u.length - s).includes(all) && s < u.length) {
      s += 1
    }
    return all.substring(0, all.length - s)
  })

  return `${[names[0], last(names)].map(n => n.replace(commonUnit, '')).join(glue)} ${commonUnit}`
}

const CardCell = ({ children, ...props }) => (
  <Flex
    border="1px solid #efefef"
    px="4px"
    py="2px"
    alignItems="center"
    {...props}
  >
    <Text.Mono>{children}</Text.Mono>
  </Flex>
)

const DataCards = ({
  groupData,
  mainKey,
  timeKey,
  colGroupItem,
  colGroupKey,
  rowGroupKey,
  timeColor,
  item,
}) => {
  const scopedData = useMemo(() => groupData.filter(d => colGroupItem.includes(d[colGroupKey]) && d[rowGroupKey] === item), [colGroupItem, colGroupKey, groupData, item, rowGroupKey])
  const n = item.replace(/\D/g, '')
  const unit = item.replace(/[0-9\s]/g, '')
  return scopedData.length > 0 && (
    <Flex my="0.5em">
      <Flex
        width="74px"
        justifyContent="center"
        alignItems="center"
        bg="#234b69"
        color="white"
        border="1px solid #efefef"
      >
        <Box textAlign="center" fontSize="17px">
          {n}<br />{unit}
        </Box>
      </Flex>
      <Flex flex="1" flexDirection="column">
        {scopedData.map((d, m) => (
          <Flex
            key={m}
            fontSize="16px"
            flex="1"
          >
            <CardCell
              flex="1"
              bg="white"
            >{d[mainKey]}</CardCell>
            {d[timeKey] && (
              <CardCell
                bg={timeColor}
                color="white"
              >{d[timeKey]}</CardCell>
            )}
          </Flex>
        ))}
      </Flex>
    </Flex>
  )
}

const GradientGroup = styled(Box)`
  position: relative;
  background: linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3));
  ${props => props['data-name'] && `
    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      top: 4px;
      bottom: 0;
      border-left: 1px solid black;
    }
    &::after {
      content: attr(data-name);
      font-size: 15px;
      display: block;
      position: absolute;
      left: -24px;
      top: 50%;
      writing-mode: vertical-lr;
      text-orientation: mixed;
      transform: rotate(180deg) translateY(50%);
    }
  `}
`

const PortfolioTableRender = ({
  data,
  colGroup,
  colGroupKey,
  rowGroup,
  rowGroupKey,
  mainKey,
  timeKey,
  timeColor,
  name,
}) => {
  const scaleRef = useRef()
  useEffect(() => {
    const groupHeight = {}
    const groups = {}
    const scaleRes = /scale\((\d+(\.\d+)?)\)/.exec(scaleRef.current?.style.transform)
    scaleRef.current?.querySelectorAll('.same-height').forEach(el => {
      const id = el.dataset.id
      el.style.minHeight = 'auto'
      const { height } = el.getBoundingClientRect()
      groupHeight[id] = Math.max(groupHeight[id] || 0, height / (scaleRes?.[1] || 1))
      groups[id] = groups[id] || []
      groups[id].push(el)
    })

    Object.keys(groups).forEach(id => {
      groups[id].forEach(el => {
        el.style.minHeight = `${groupHeight[id]}px`
      })
    })

  }, [JSON.stringify({ colGroup, rowGroup, data })])

  return (
    <PageSimulator ref={scaleRef} name={`${name}-portfolio`}>
      <Flex pl="92px" pr="76px" pt="146px" justifyContent="center">
        <Box>
          <Flex.HStack spacing="34px">
            {data.map(([groupName, groupData], i) => {
              const colGroupItems = getGroupItems(groupData, colGroup[groupName], colGroupKey)
              const rowGroupItems = getGroupItems(groupData, rowGroup[groupName], rowGroupKey)
              return (
                <Flex key={i} flexDirection="column">
                  <Box fontSize="25px" bg="#c63b38" color="white" textAlign="center" py="6px" border="2px solid white">
                    {groupName}
                  </Box>
                  <Flex.HStack flex="1">
                    {colGroupItems.map((colGroupItem, j) => {
                      return (
                        <Flex key={j} width="275px" flexDirection="column">
                          <Box bg="#cacaca" textAlign="center" fontSize="23px" py="7px" border="2px solid white">
                            {joinName(colGroupItem, '/')}
                          </Box>
                          {rowGroupItems.map((rowGroupItem, k) => {
                            const sorted = sortSize(rowGroupItem)
                            return (
                              <GradientGroup
                                key={k}
                                p="0.5em"
                                border="2px solid white"
                                className="same-height"
                                data-id={`${k + 1}/${rowGroupItems.length}`}
                                data-name={j === 0 && joinName([...sorted].reverse(), ' - ')}
                              // flex={k + 1 === rowGroupItems.length && '1'}
                              >
                                {sorted.map((item, l) => (
                                  <DataCards
                                    key={l}
                                    groupData={groupData}
                                    colGroupItem={colGroupItem}
                                    colGroupKey={colGroupKey}
                                    rowGroupKey={rowGroupKey}
                                    item={item}
                                    mainKey={mainKey}
                                    timeKey={timeKey}
                                    timeColor={timeColor}
                                  />
                                ))}
                              </GradientGroup>
                            )
                          })}
                        </Flex>
                      )
                    })}
                  </Flex.HStack>
                </Flex>
              )
            })}
          </Flex.HStack>
          <Flex.HStack mt="16px" justifyContent="flex-end" alignItems="center" spacing="16px">
            <NoteBox bg={timeColor}>{timeKey}</NoteBox>
            <NoteBox bg="#9fa0a0">NOTE</NoteBox>
            <Text>Roapmaps are subject to change.</Text>
          </Flex.HStack>
        </Box>
      </Flex>
    </PageSimulator>
  )
}

export default PortfolioTableRender
